<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process165"></a>

<p><br></p>
<p><span class="rvts17">一网欲望（</span><span class="rvts50">A Web of Desire</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">任务坐标：</span>
<br><span class="rvts20">X:389,Y:792</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：颓废者多罗特亚（</span><span class="rvts20">The Decadent One, Dorotya</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">与多罗特亚接吻，获得新的天赋</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1143.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">该任务在浮木城酒吧的下层（竞技场旁边的休息区域），找到多罗特亚，她会要求与你接吻。但是她要求只能单独与一个角色进行接吻（其它队友可以暂时分散出去）。</span>
<br><span class="rvts21">之后跟随她来到这里，开启对话，任务完成后获得</span><span class="rvts20"></span><span class="rvts21">蜘蛛之吻</span><span class="rvts20"></span><span class="rvts21">天赋。之后的其它队员也可以分别和她接吻获得该天赋。</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">在与她对话中，会选择所看到的镜像，这样将会导致蜘蛛之吻天赋最终的效果。（如果有学者天赋，她会提示你这一点）：</span>
<br><span class="rvts20">1. </span><span class="rvts81">高松的橡树（</span><span class="rvts82">A Towering Oak</span><span class="rvts81">）</span><span class="rvts21">：</span><span class="rvts20">-2</span><span class="rvts21">体质，</span><span class="rvts20">+2</span><span class="rvts21">力量</span>
<br><span class="rvts20">2. </span><span class="rvts81">弯曲的刚毛（</span><span class="rvts82">A Curved quill</span><span class="rvts81">）</span><span class="rvts21">：</span><span class="rvts20">+2</span><span class="rvts21">主动性，</span><span class="rvts20">-2</span><span class="rvts21">体质</span>
<br><span class="rvts20">3. </span><span class="rvts81">一个金箱子（</span><span class="rvts82">A Golden Chest</span><span class="rvts81">）</span><span class="rvts21">：</span><span class="rvts20">+2000</span><span class="rvts21">金币，</span><span class="rvts20">-2</span><span class="rvts21">体质</span>
<br><span class="rvts20">4. </span><span class="rvts81">一条龙（</span><span class="rvts82">A Dragon</span><span class="rvts81">）</span><span class="rvts21">：</span><span class="rvts20">-2</span><span class="rvts21">体质，</span><span class="rvts20">+2</span><span class="rvts21">智谋</span>
<br><span class="rvts20">5. </span><span class="rvts81">一个蝶蛹（</span><span class="rvts82">A Chrysalis</span><span class="rvts81">）</span><span class="rvts21">：</span><span class="rvts20">1</span><span class="rvts21">个闪光重生布偶，</span><span class="rvts20">-2</span><span class="rvts21">体质</span>
<br><span class="rvts20"></span>
<br><span class="rvts21">另外，可以尝试偷窃她身上的宝物，她身上有重生布偶（用于战斗中死亡后原地复活）。在她屋子的桌子上有一把钥匙，可以打开她的保险箱（里面没有任何值钱东西）。</span>
<br><span class="rvts21">也可以尝试杀掉她，获得两件史诗匕首。</span>
<br><span class="rvts21">获得</span><span class="rvts20"></span><span class="rvts21">蜘蛛之吻</span><span class="rvts20"></span><span class="rvts21">天赋，可以跳过在石墓花园里面的蜘蛛战斗。（详情见石墓庄园的</span><span class="rvts20"></span><span class="rvts21">织网者（</span><span class="rvts20">The Weaver</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">章节）</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



